.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
@media screen and (max-width: 36rem) {
  .container {
    max-width: 31.875rem;
  }
}
@media screen and (max-width: 48rem) {
  .container {
    max-width: 43.125rem;
  }
}
@media screen and (max-width: 62rem) {
  .container {
    max-width: 58.125rem;
  }
}
@media screen and (max-width: 75rem) {
  .container {
    max-width: 69.375rem;
  }
}

:root {
  --border-color: #dde2e7;
  --text-color: #1a1a1a;
  --timepublish-color: #b0b0b0;
  --text-btn-color: #ffffff;
  --background-btn-color: #375573;
  --footer-background-color: #dde2e7;
  --footer-text-color: #8e9399;
  --footer-copyright-background: #e8ebee;
  --footer-copyright-text: #b7babe;
  --pagination-normal-color: #0c0c0c;
  --pagination-active-color: #e84c50;
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body,
html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  width: 100%;
  box-sizing: border-box;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.9375rem;
  margin-right: -0.9375rem;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Bold.eot");
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/Montserrat/Montserrat-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Regular.eot");
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/Montserrat/Montserrat-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Medium.eot");
  src: local("Montserrat Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat/Montserrat-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Playfair Display";
  src: url("../fonts/Playfair/PlayfairDisplay-Regular.woff2") format("woff2") url("../fonts/Playfair/PlayfairDisplay-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
body,
html {
  font-family: "Montserrat", sans-serif;
  font-size: 100%;
  text-wrap: balance;
}

.social-media {
  display: flex;
  justify-content: center;
  align-items: center;
}
.social-media__item {
  display: flex;
  margin-right: 1.875rem;
}
.social-media__item:last-child {
  margin-right: 0rem;
}
.social-media__link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0.375rem;
}
.social-media__image {
  width: 1.375rem;
  height: 1.375rem;
}

.post {
  padding-top: 3.4375rem;
}
.post__title {
  font-size: 2.625em;
  font-weight: 400;
  font-family: "Playfair Display", sans-serif;
  text-align: center;
  color: var(--text-color);
}
.post__info {
  text-align: center;
  margin-top: 1.5625rem;
  margin-bottom: 2.5rem;
}
.post__timepublish {
  font-size: 0.875em;
  font-weight: 700;
  color: var(--timepublish-color);
}
.post__commentscount {
  font-size: 0.875em;
  font-weight: 700;
  color: var(--timepublish-color);
}
.post__content {
  width: 100%;
}
.post__image {
  width: 100%;
  height: 360px;
  object-fit: cover;
}
.post__description {
  font-size: 1em;
  font-weight: 400;
  line-height: 1.75em;
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
  color: var(--text-color);
}

.post-more {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 2.5rem;
  border-bottom: 0.0625rem solid var(--border-color);
}
.post-more__btn {
  font-size: 0.75em;
  font-weight: 500;
  display: flex;
  align-items: center;
  padding: 1.125rem 0rem 1.125rem 1.875rem;
  border-radius: 0.375rem;
  text-transform: uppercase;
  color: var(--text-btn-color);
  background-color: var(--background-btn-color);
}
.post-more__btn__arrow {
  width: 0.9375rem;
  height: 0.9375rem;
  padding-left: 0.625rem;
  padding-right: 1.875rem;
  color: var(--text-btn-color);
}
.post-more__socials {
  display: flex;
}
.post-more__socials__item {
  padding-right: 1.25rem;
}
.post-more__socials__link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0.375rem;
}
.post-more__socials__item:last-child {
  padding-right: 0rem;
}
.post-more__socials__image {
  width: 1.25rem;
  height: 1.25rem;
}

.micro-post {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1.5rem;
  padding-bottom: 2.1875rem;
  border-bottom: 0.0625rem solid var(--border-color);
}
.micro-post:last-child {
  border-bottom: inherit;
}
.micro-post__image {
  width: 5rem;
  height: 5rem;
  object-fit: cover;
  border-radius: 50%;
}
.micro-post__description {
  font-size: 1.375em;
  font-weight: 400;
  font-family: "Playfair Display", sans-serif;
  display: block;
  text-align: center;
  margin-top: 1.5rem;
  margin-bottom: 0.875rem;
  color: var(--text-color);
}
.micro-post__info {
  display: flex;
  flex-wrap: wrap;
}
.micro-post__timepublish {
  font-size: 0.75em;
  font-weight: 500;
  color: var(--timepublish-color);
}
.micro-post__commentscount {
  font-size: 0.75em;
  font-weight: 500;
  color: var(--timepublish-color);
}

.fancybox-gallery {
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.9375rem;
  margin-right: -0.9375rem;
}
.fancybox-gallery__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  width: calc(50% - 1.875rem);
  margin-top: 0.9375rem;
  margin-bottom: 0.9375rem;
}
@media screen and (max-width: 62rem) {
  .fancybox-gallery__item {
    width: calc(25% - 1.875rem);
  }
}
@media screen and (max-width: 48rem) {
  .fancybox-gallery__item {
    width: calc(33.3333333333% - 1.875rem);
  }
}
@media screen and (max-width: 36rem) {
  .fancybox-gallery__item {
    width: calc(50% - 1.875rem);
  }
}
.fancybox-gallery__preview {
  display: block;
  width: 120px;
  height: 100px;
  object-fit: cover;
}

.pagination {
  width: 100%;
  display: flex;
  align-self: baseline;
}
.pagination__list {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
}
.pagination__item {
  display: flex;
  margin: 0 0.5rem;
}
.pagination__item:last-child {
  margin-right: 0;
}
.pagination__item__older_post {
  margin-left: auto;
  margin-right: 0;
}
.pagination__link {
  font-size: 1.125em;
  font-weight: bold;
  padding: 0.5rem;
  color: var(--pagination-normal-color);
}
.pagination__link.active, .pagination__link:hover, .pagination__link:focus {
  color: var(--pagination-active-color);
}
.pagination__link__older_post {
  font-size: 0.875em;
  font-weight: bold;
  text-transform: uppercase;
  padding-right: 0;
  display: inline-flex;
  align-items: center;
  align-self: baseline;
}
.pagination__link__older_post::after {
  content: "";
  display: inline-block;
  width: 13px;
  height: 10px;
  margin-top: 0.125rem;
  margin-left: 0.8125rem;
  background-image: url("../img/icons/ic_angle_double_right.svg");
  background-repeat: no-repeat;
}

.header {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid #dde2e7;
}
.header__left-side {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  width: calc(16.6666666667% - 1.875rem);
}
@media screen and (max-width: 75rem) {
  .header__left-side {
    width: calc(100% - 1.875rem);
  }
}
@media screen and (max-width: 75rem) {
  .header__left-side {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    order: 1;
  }
}
.header__centered {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  width: calc(66.6666666667% - 1.875rem);
}
@media screen and (max-width: 75rem) {
  .header__centered {
    width: calc(100% - 1.875rem);
  }
}
.header__right-side {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  width: calc(16.6666666667% - 1.875rem);
}
@media screen and (max-width: 75rem) {
  .header__right-side {
    width: calc(100% - 1.875rem);
  }
}
@media screen and (max-width: 75rem) {
  .header__right-side {
    order: 1;
    margin-bottom: 0.375rem;
  }
}

.header__left-side {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-content {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  width: calc(100% - 1.875rem);
}
.header-content__logo {
  display: flex;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 55px;
  margin: auto;
  flex-basis: content;
}
@media screen and (max-width: 75rem) {
  .header-content__logo {
    padding: 0.75rem 0;
  }
}

.header-search {
  display: flex;
}
.header-search__btn {
  padding: 6px;
  background: none;
  border: none;
}
.header-search__btn__image {
  width: 25px;
  height: 25px;
}

.header__right-side {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__right-side__btn {
  font-size: 0.75em;
  font-weight: 500;
  display: flex;
  align-items: center;
  padding: 0.9375rem 2.1875rem 0.9375rem 2.1875rem;
  border-radius: 1.1875rem;
  text-transform: uppercase;
  color: var(--text-btn-color);
  background-color: var(--background-btn-color);
}

.section-general {
  padding-bottom: 4.6875rem;
}
.section-general .post-content,
.section-general .microblog-content {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 62rem) {
  .section-general .post-content,
  .section-general .microblog-content {
    width: calc(100% - 1.875rem);
  }
}
.section-general .post-content {
  width: calc(75% - 1.875rem);
}
.section-general .microblog-content {
  width: calc(25% - 1.875rem);
}
.section-general .microblog-content .gallery {
  margin-top: 3.75rem;
}
.section-general .recent-post {
  padding-top: 60px;
}
.section-general .recent-post,
.section-general .popular-post {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.section-general .recent-post__title,
.section-general .popular-post__title {
  font-size: 1.125em;
  font-weight: bold;
  padding-top: 1.375rem;
  padding-bottom: 1.375rem;
  margin-bottom: 0.625rem;
  width: 100%;
  display: block;
  text-align: center;
  color: var(--text-color);
  border-radius: 0.25rem;
  border: 0.0625rem solid var(--border-color);
}
@media screen and (max-width: 62rem) {
  .section-general .recent-post,
  .section-general .popular-post {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }
}
.section-general .recent-post .micro-post,
.section-general .popular-post .micro-post {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  border-bottom: none;
}
@media screen and (max-width: 62rem) {
  .section-general .recent-post .micro-post,
  .section-general .popular-post .micro-post {
    width: calc(33.3333333333% - 1.875rem);
  }
}
@media screen and (max-width: 48rem) {
  .section-general .recent-post .micro-post,
  .section-general .popular-post .micro-post {
    width: calc(50% - 1.875rem);
  }
}
@media screen and (max-width: 36rem) {
  .section-general .recent-post .micro-post,
  .section-general .popular-post .micro-post {
    width: calc(100% - 1.875rem);
  }
}

.footer {
  display: flex;
  flex-direction: column;
  background: var(--footer-background-color);
}
.footer .social-media {
  margin-top: 3.75rem;
}
@media screen and (max-width: 62rem) {
  .footer .social-media {
    margin-top: 0;
    margin-bottom: 0.75rem;
  }
}

.footer-content,
.footer-socials {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  display: flex;
}
@media screen and (max-width: 62rem) {
  .footer-content,
  .footer-socials {
    width: calc(100% - 1.875rem);
  }
}

.footer-content {
  width: calc(75% - 1.875rem);
}
.footer-content__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 3.75rem;
  margin-bottom: 4.375rem;
  width: 100%;
}
@media screen and (max-width: 62rem) {
  .footer-content__list {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
  }
}
.footer-content__item {
  width: 33%;
  padding-bottom: 1.25rem;
}
@media screen and (max-width: 48rem) {
  .footer-content__item {
    width: 44.44%;
  }
}
@media screen and (max-width: 36rem) {
  .footer-content__item {
    width: 100%;
  }
}
.footer-content__link {
  font-size: 1em;
  font-weight: 500;
  color: var(--footer-text-color);
}

.footer-socials {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.footer-socials__list {
  display: flex;
  margin-top: 3.75rem;
}
.footer-socials__item {
  padding-right: 1.875rem;
}
.footer-socials__link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 6px;
}
.footer-socials__image {
  width: 1.375rem;
  height: 1.375rem;
}

.footer-copyright {
  width: calc(100% - 1.875rem);
  background: var(--footer-copyright-background);
  width: 100%;
}
.footer-copyright__text {
  display: block;
  text-align: center;
  width: 100%;
  margin-top: 1.5625rem;
  margin-bottom: 1.5625rem;
  font-size: 0.875em;
  font-weight: 500;
  color: var(--footer-copyright-text);
}